        margin-left: 90px;
        line-height: 32px;
    }
    .el-checkbox {
        margin-left: 0;
        margin-right: 14px;
    }
    /deep/ .el-checkbox__input.is-checked + .el-checkbox__label {
        color: $greyDefault;
    }
}
</style>
