<template>
    <div :class="['setting-panel', {'actived': this.showPanel}]">
        <div class="toggle-button" @click="togglePanel">
            <i :class="['common-icon-arrow-left', {'arrow-right': this.showPanel}]"></i>
        </div>
        <bk-tab class="panel-content" :active-name="activeTab" @tab-changed="onTabChange">
            <bk-tabpanel name="global-variable-tab" :title="i18n.global_varibles">
                <TabGlobalVariables
                    ref="globalVariable"
                    class="panel-item"
                    :isVariableEditing="isVariableEditing"
                    @changeVariableEditing="changeVariableEditing"
                    @variableDataChanged="variableDataChanged"
                    @onDeleteConstant="onDeleteConstant">
                </TabGlobalVariables>
            </bk-tabpanel>
            <bk-tabpanel name="template-config-tab" :title="i18n.common">
                <TabTemplateConfig
                    class="panel-item"
                    :isTemplateConfigValid="isTemplateConfigValid"
                    :businessInfoLoading="businessInfoLoading"
                    @settingsTemplateConfigVaild="settingsTemplateConfigVaild">
                </TabTemplateConfig>
            </bk-tabpanel>
            <bk-tabpanel name="local-draft-tab" :title="i18n.draft">
                <TabLocalDraft
                    class="panel-item"
                    :draftArray="draftArray"
                    @onDeleteDraft="onDeleteDraft"
                    @onReplaceTemplate="onReplaceTemplate"
                    @onNewDraft="onNewDraft"
                    @hideConfigPanel="hideConfigPanel">
                </TabLocalDraft>
            </bk-tabpanel>
        </bk-tab>
    </div>
</template>
<script>
import '@/utils/i18n.js'
import { mapState, mapMutations } from 'vuex'
import tools from '@/utils/tools.js'
import draggable from 'vuedraggable'
import BaseCheckbox from '@/components/common/base/BaseCheckbox.vue'
import BaseInput from '@/components/common/base/BaseInput.vue'
import TabGlobalVariables from './TabGlobalVariables.vue'
import TabTemplateConfig from './TabTemplateConfig.vue'
import TabLocalDraft from './TabLocalDraft.vue'
import NoData from '@/components/common/base/NoData.vue'

import draft from '@/utils/draft.js'
import {NAME_REG} from '@/constants/index.js'

export default {
    name: 'TemplateSetting',
    components: {
        TabGlobalVariables,
        TabTemplateConfig,
        TabLocalDraft
    },
    props: ['businessInfoLoading', 'isTemplateConfigValid', 'isSettingPanelShow', 'draftArray', 'localTemplateData', 'isClickDraft'],
    data () {
        return {
            i18n: {
                global_varibles: gettext(''),
                common: gettext(''),
                draft: gettext('')
            },
            showPanel: true,
            activeTab: 'global-variable-tab',
            isVariableEditing: false
        }
    },
    computed: {
        ...mapState({
            'businessBaseInfo': state => state.template.businessBaseInfo,
            'outputs': state => state.template.outputs,
            'constants': state => state.template.constants,
            'timeout': state => state.template.time_out
        }),
        variableData () {
            if (this.theKeyOfEditing) {
                return this.constants[this.theKeyOfEditing]
            } else {
                return {
                    custom_type: 'input',
                    desc: '',
                    key: '',
                    name: '',
                    show_type: 'show',
                    source_info: {},
                    source_tag: '',
                    source_type: 'custom',
                    validation: '^.+$',
                    validator: [],
                    value: ''
                }
            }
        }
    },
    watch: {
        isTemplateConfigValid (val) {
            if (!val) {
                this.activeTab = 'template-config-tab'
            }
        },
        isSettingPanelShow (val) {
            this.showPanel = val
        }
    },
    methods: {
        ...mapMutations ('template/', [
            'editVariable',
            'deleteVariable',
            'setOutputs',
            'setReceiversGroup',
            'setNotifyType',
            'setOvertime',
            'setCategory'
        ]),
        togglePanel () {
            this.$emit('toggleSettingPanel', !this.showPanel)
        },
        // 
        isEditPanelOpen () {
            return this.isVariableEditing
        },
        // 
        saveVariable () {
            return this.$refs.globalVariable.saveVariable()
        },
        // tab
        setErrorTab (tab) {
            this.onTabChange(tab)
        },
        onTabChange (tabName) {
            this.activeTab = tabName
        },
        onDeleteConstant (key) {
            this.$emit('onDeleteConstant', key)
        },
        changeVariableEditing (val) {
            this.isVariableEditing = val
        },
        variableDataChanged () {
            this.$emit('variableDataChanged')
        },
        onSelectCategory (value) {
            this.$emit('onSelectCategory', value)
        },
        onDeleteDraft (key) {
            this.$emit('onDeleteDraft', key)
        },
        onReplaceTemplate (data) {
            this.$emit('onReplaceTemplate', data)
        },
        onNewDraft (name) {
            this.$emit('onNewDraft', name)
        },
        hideConfigPanel () {
            this.$emit('hideConfigPanel')
        },
        updateLocalTemplateData () {
            this.$emit('updateLocalTemplateData')
        },
        settingsTemplateConfigVaild (value) {
            this.$emit('templateConfigVaild', value)
        }
    }
}
</script>
