<template>
    <div class="local-draft-panel">
        <div :class="{'add-draft': true, 'unfold-add-draft': newDraftShow}">
            <div class="draft-form" v-if="newDraftShow">
                <BaseInput :placeholder="i18n.draftMessage" name="draftName" v-model="newDraftName" v-validate="draftNameRule"/>
                <bk-button type="success" size="small" @click="onNewDraft">{{i18n.affirm}}</bk-button>
                <bk-button size="small" @click="onCancelNewDraft">{{i18n.cancel}}</bk-button>
            </div>
            <bk-button
                v-else
                type="primary"
                size="small"
                @click="onShowDraftForm">
                {{ i18n.newDraft }}
            </bk-button>
            <bk-tooltip placement="bottom-end" class="draft-tooltip">
                <i class="common-icon-warning"></i>
                <div slot="content">
                    <div class="tips-item" style="white-space: normal;">
                        <h4>{{ i18n.sketch }}</h4>
                        <p>
                            {{ i18n.draftSketch }}
                        </p>
                    </div>
                </div>
            </bk-tooltip>
            <span class="common-error-tip error-msg">{{ errors.first('draftName') }}</span>
        </div>
        <div class="local-draft-content">
            <div class="variable-header clearfix">
                <span class="col-drag t-head"></span>
                <span class="col-number t-head">{{ i18n.serialNumber }}</span>
                <span class="col-message t-head">{{ i18n.draftMessage }}</span>
                <span class="col-time t-head">{{ i18n.saveTime }}</span>
                <span class='col-delete t-head'>{{ i18n.delete }}</span>
            </div>
            <ul class="draft-list">
                <draggable
                    v-model="draftArray"
                    :options="{handle:'.col-drag'}">
                        <li
                            v-for="(draft, index) in draftArray"
                            :key="draft.key"
                            :class="{
                                'clearfix': true,
                                'draft-item': true
                            }"
                            @click="onReplaceTemplate(draft.data.template)">
                            <div class="draft-content">
                                <span class="col-item col-drag">
                                </span>
                                    <span class="col-item col-number">
                                    {{index + 1}}
                                </span>
                                <span class="col-item col-message" :title="draft.data.description.message">
                                    {{draft.data.description.message}}
                                </span>
                                <span class="col-item col-time">
                                    {{draft.data.description.time}}
                                </span>
                                <span class="col-item col-delete" @click.stop="onDeleteDraft(draft.key)">
                                    <i class="common-icon-close"></i>
                                </span>
                            </div>
                        </li>
                </draggable>
                <li v-if="!draftArray.length" class="empty-draft-tip">
                    <NoData>
                        <p>{{i18n.emptyDraftTip}}</p>
                    </NoData>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import '@/utils/i18n.js'
import draggable from 'vuedraggable'
import { NAME_REG, STRING_LENGTH } from '@/constants/index.js'
import BaseInput from '@/components/common/base/BaseInput.vue'
import NoData from '@/components/common/base/NoData.vue'

export default {
    name: 'TabLocalDraft',
    components: {
        draggable,
        BaseInput,
        NoData
    },
    props: ['draftArray'],
    data () {
        return {
            replaceData: null,
            newDraftShow: false,
            newDraftName: '',
            draftNameRule: {
                required: true,
                max: STRING_LENGTH.DRAFT_NAME_MAX_LENGTH,
                regex: NAME_REG
            },
            i18n: {
                newDraft: gettext(''),
                draftMessage: gettext(''),
                replace: gettext(''),
                saveTime: gettext(''),
                delete: gettext(''),
                emptyDraftTip: gettext(''),
                replaceTips: gettext(''),
                replaceConfirm: gettext(''),
                serialNumber: gettext(''),
                draftName: gettext(''),
                sketch: gettext(''),
                draftSketch: gettext('50'),
                // produce: gettext(''),
                // draftProduce: gettext(''),
                // draftProduce1: gettext('5'),
                // draftProduce2: gettext('""'),
                // draftProduce3: gettext(''),
                // draftProduce4: gettext(''),
                // deleteMethod: gettext(''),
                // draftDelete: gettext(''),
                // draftDelete1: gettext(''),
                // draftDelete2: gettext(' localStorage '),
                affirm: gettext(''),
                cancel: gettext(''),
                resetTemplate: gettext('')
            }
        }
    },
    methods: {
        // 
        onReplaceTemplate (templateData) {
            if (!this.isClickDraft) {
                this.$emit('updateLocalTemplateData')
            }
            const data = {
                templateData: templateData,
                type: 'replace'
            }
            this.$emit('onReplaceTemplate', data)
            this.$emit('hideConfigPanel')
        },
        // 
        onDeleteDraft (key) {
            this.$emit('onDeleteDraft', key)
        },
        // 
        onShowDraftForm () {
            this.newDraftShow = true
            this.$emit('hideConfigPanel')
        },
        onNewDraft () {
            this.$validator.validateAll().then((result) => {
                if (!result) {
                    return
                }
                // 
                this.$emit('onNewDraft', this.newDraftName)
                this.newDraftName = ''
                this.newDraftShow = false
                this.$emit('hideConfigPanel')
            })
        },
        onCancelNewDraft () {
            this.newDraftName = ''
            this.newDraftShow = false
        }
    }
}
</script>
